.thank-section {
	position: relative;
	padding: 5rem 0 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		margin: 0 auto;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding-bottom: 15rem;
		@include max-md {
			padding-bottom: 10rem;
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					object-position: 50% 100%;
				}
			}
		}
		.orn-1 {
			position: absolute;
			bottom: 10%;
			left: 8%;
			width: 60px;
			@include min-md {
				left: 20%;
				width: 80px;
			}
			@include min-lg {
				bottom: 30%;
				left: 25%;
				width: 100px;
			}
		}
		.orn-2 {
			position: absolute;
			bottom: 5%;
			left: 40%;
			width: 40px;
			transform: translate(-50%, 0);
			@include min-md {
				left: 50%;
				width: 60px;
			}
			@include min-lg {
				width: 80px;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 15%;
			right: 28%;
			width: 50px;
			transform: translate(-50%, 0);
			@include min-md {
				bottom: 20%;
				right: 20%;
				width: 70px;
			}
			@include min-lg {
				bottom: 30%;
				width: 90px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 8%;
			right: 0;
			width: 60px;
			transform: translate(20%, 0);
			@include min-md {
				right: 5%;
				width: 80px;
				transform: translate(0, 0);
			}
			@include min-lg {
				width: 100px;
			}
		}
		.orn-5 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 240px;
			transform: translate(-65%, -50%);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 260px;
			transform: translate(40%, -20%) scaleX(-1);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 0;
			width: 260px;
			transform: translate(-50%, -40%);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			background: var(--bg-gradient);
			color: var(--color-dark);
			box-shadow: 0.5rem 0.5rem 2rem rgba($color: #000000, $alpha: 0.15);
			border-radius: var(--border-radius);
			margin-top: -5rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
