.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0) rotate(10deg) scaleX(-1);
		@include min-md {
			width: 140px;
			transform: translate(-40%, 0) rotate(10deg) scaleX(-1);
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(50%, 0) rotate(-10deg);
		@include min-md {
			width: 140px;
			transform: translate(40%, 0) rotate(-10deg);
		}
		@include min-lg {
			width: 160px;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
