.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.bg-couple {
		position: relative;
		width: 100%;
		.overlay-elements-gradient {
			&.overlay-bottom {
				bottom: -5px;
			}
		}
		@include max-md {
			aspect-ratio: 1;
			.image-element {
				height: 100%;
				.img-fluid {
					height: 100%;
					object-fit: cover;
				}
			}
		}
		.orn-1 {
			position: absolute;
			bottom: 8%;
			left: 5%;
			width: 60px;
			@include min-md {
				left: 20%;
				width: 80px;
			}
			@include min-lg {
				bottom: 17%;
				left: 25%;
				width: 100px;
			}
		}
		.orn-2 {
			position: absolute;
			bottom: 5%;
			left: 45%;
			width: 60px;
			transform: translate(-50%, 0);
			@include min-md {
				left: 50%;
				width: 80px;
			}
			@include min-lg {
				width: 100px;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 8%;
			right: 25%;
			width: 50px;
			transform: translate(-20%, 0);
			@include min-md {
				bottom: 20%;
				width: 60px;
			}
			@include min-lg {
				width: 80px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 5%;
			right: 0;
			width: 60px;
			transform: translate(40%, 0);
			@include min-md {
				right: 5%;
				width: 80px;
				transform: translate(0, 0);
			}
			@include min-lg {
				width: 100px;
			}
		}
		.orn-5 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 240px;
			transform: translate(-65%, -50%);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 260px;
			transform: translate(40%, -20%) scaleX(-1);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 0;
			width: 260px;
			transform: translate(-50%, -40%);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
	}
	.title-section {
		position: absolute;
		left: 50%;
		top: 5rem;
		transform: translate(-50%, 0);
	}
	.couple-wrapper {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
			.gap-couple {
				gap: 3rem;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 320px;
			transform: rotate(-20deg);
			@include min-md {
				left: 40%;
			}
			@include min-lg {
				width: 360px;
			}
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 80%;
			transform: translate(30%, 0);
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(30%, 0);
			@include min-md {
				transform: translate(20%, 0);
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 50%;
			width: 320px;
			transform: rotate(20deg) scaleX(-1);
			@include min-md {
				right: 40%;
			}
			@include min-lg {
				width: 360px;
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 80%;
			transform: translate(-30%, 0) scaleX(-1);
			@include min-md {
				transform: translate(-20%, 0) scaleX(-1);
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-30%, 0) scaleX(-1);
		}
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/9;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
		&.man {
			text-align: left;
			.image-wrap,
			.sosmed-wrap {
				margin-right: auto;
			}
		}
		&.woman {
			text-align: right;
			.image-wrap,
			.sosmed-wrap {
				margin-left: auto;
			}
		}
	}
}
