.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-40%, 0);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(30%, 0) rotate(20deg) scaleX(-1);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 120px;
		transform: translate(50%, 0) rotate(-20deg);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(40%, 40%) scaleX(-1);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 0 2rem;
		@include max-md {
			padding: 0 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
